___CSS_LOADER_EXPORT___.push([module.id, `.article {
  width: 387px;
  display: block;
  filter: drop-shadow(0px 20px 25px rgba(0, 0, 0, 0.1));
}
@media (max-width: 1250px) {
  .article {
    width: 350px;
  }
}
@media (max-width: 767px) {
  .article {
    width: 320px;
  }
}
@media (max-width: 359px) {
  .article {
    width: 100%;
  }
}
.article__img {
  height: 230px;
}
.article__wrapper {
  padding: 20px 30px 30px;
  background: var(--primary---white);
}
@media (max-width: 1120px) {
  .article__wrapper {
    padding: 20px 20px 30px;
  }
}
.article__date {
  color: var(--secondary---text);
  font-size: 16px;
  font-weight: 400;
  line-height: 180%; /* 28.8px */
  letter-spacing: 0.32px;
}
.article__title {
  padding-right: 25px;
  color: var(--primary---content);
  font-size: 20px;
  font-weight: 600;
  line-height: 25px; /* 125% */
}
@media (max-width: 1120px) {
  .article__title {
    padding-right: 22px;
  }
}
@media (max-width: 767px) {
  .article__title {
    padding-right: 0;
    font-size: 18px;
  }
}`, "",{"version":3,"sources":["webpack://./src/components/article/index.scss"],"names":[],"mappings":"AAAA;EACE,YAAA;EACA,cAAA;EAEA,qDAAA;AAAF;AAEE;EANF;IAOI,YAAA;EACF;AACF;AACE;EAVF;IAWI,YAAA;EAEF;AACF;AAAE;EAdF;IAeI,WAAA;EAGF;AACF;AADE;EACE,aAAA;AAGJ;AAAE;EACE,uBAAA;EACA,kCAAA;AAEJ;AAAI;EAJF;IAKI,uBAAA;EAGJ;AACF;AAAE;EACE,8BAAA;EAEA,eAAA;EACA,gBAAA;EACA,iBAAA,EAAA,WAAA;EACA,sBAAA;AACJ;AAEE;EACE,mBAAA;EACA,+BAAA;EAEA,eAAA;EACA,gBAAA;EACA,iBAAA,EAAA,SAAA;AADJ;AAGI;EARF;IASI,mBAAA;EAAJ;AACF;AAEI;EAZF;IAaI,gBAAA;IACA,eAAA;EACJ;AACF","sourcesContent":[".article {\n  width: 387px;\n  display: block;\n\n  filter: drop-shadow(0px 20px 25px rgba(0, 0, 0, 0.1));\n\n  @media (max-width: 1250px) {\n    width: 350px;\n  }\n\n  @media (max-width: 767px) {\n    width: 320px;\n  }\n\n  @media (max-width: 359px) {\n    width: 100%;\n  }\n\n  &__img {\n    height: 230px;\n  }\n\n  &__wrapper {\n    padding: 20px 30px 30px;\n    background: var(--primary---white);\n\n    @media (max-width: 1120px) {\n      padding: 20px 20px 30px;\n    }\n  }\n\n  &__date {\n    color: var(--secondary---text);\n\n    font-size: 16px;\n    font-weight: 400;\n    line-height: 180%; /* 28.8px */\n    letter-spacing: 0.32px;\n  }\n\n  &__title {\n    padding-right: 25px;\n    color: var(--primary---content);\n\n    font-size: 20px;\n    font-weight: 600;\n    line-height: 25px; /* 125% */\n\n    @media (max-width: 1120px) {\n      padding-right: 22px;\n    }\n\n    @media (max-width: 767px) {\n      padding-right: 0;\n      font-size: 18px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
